import { FORM_BLOCK, FORM_CONTROL, SIDEBAR_TAB_TRIGGER } from '../../components/Sidebar/constants';
export class KeyParametersPlugin {
    constructor(api) {
        this.api = api;
        this._sidebarLayout = [
            {
                block: FORM_BLOCK.HEADER,
                size: 'h5',
                label: 'Position',
                underline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'x',
                label: 'X',
                half: true,
                inline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'y',
                label: 'Y',
                half: true,
                inline: true,
            },
            {
                block: FORM_BLOCK.HEADER,
                size: 'h5',
                label: 'Size',
                underline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'width',
                label: 'X',
                half: true,
                inline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'height',
                label: 'Y',
                half: true,
                inline: true,
            },
            {
                block: FORM_BLOCK.HEADER,
                size: 'h5',
                label: 'Pivot and angle',
                underline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'pivot_x',
                label: 'X',
                half: true,
                inline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'pivot_y',
                label: 'Y',
                half: true,
                inline: true,
            },
            {
                control: FORM_CONTROL.INPUT,
                key: 'angle',
                label: 'Angle',
                inline: true,
            },
            {
                block: FORM_BLOCK.HEADER,
                size: 'h5',
                label: 'Color',
                underline: true,
            },
            {
                control: FORM_CONTROL.COLOR,
                label: 'Keycap',
                key: 'color',
                inline: true,
            },
        ];
        this._handleCreate = (tabApi) => {
            const keyCap = this.api.layoutEditor.getKeyboard().getSelectedKeyCap();
            if (keyCap) {
                tabApi.patchValue({
                    x: keyCap.x,
                    y: keyCap.y,
                    width: keyCap.width,
                    height: keyCap.height,
                    pivot_x: keyCap.pivot.x,
                    pivot_y: keyCap.pivot.y,
                    angle: keyCap.angle,
                    color: keyCap.color,
                });
            }
            tabApi.subscribe(this._handleFormChange);
        };
        // TODO: refactor
        this._handleFormChange = (data, changedKey) => {
            const keyCap = this.api.layoutEditor.getKeyboard().getSelectedKeyCap();
            if (keyCap) {
                switch (changedKey) {
                    case 'x':
                    case 'y':
                        keyCap.moveTo(Object.assign(Object.assign({}, keyCap.position), { [changedKey]: data[changedKey] }));
                        break;
                    case 'width':
                    case 'height':
                        keyCap.setSize(Object.assign(Object.assign({}, keyCap.size), { [changedKey]: data[changedKey] }));
                        break;
                    case 'pivot_x':
                    case 'pivot_y': {
                        const key = changedKey === 'pivot_x' ? 'x' : 'y';
                        keyCap.setPivot(Object.assign(Object.assign({}, keyCap.pivot), { [key]: data[changedKey] }));
                        break;
                    }
                    case 'angle':
                        keyCap.setAngle(data[changedKey]);
                        break;
                    case 'color':
                        keyCap.setColor(data[changedKey]);
                        break;
                }
            }
        };
        api.sidebar.registerTab(this, this._sidebarLayout, {
            tabName: 'Key Parameters',
            trigger: SIDEBAR_TAB_TRIGGER.KEY_SELECTED,
            onCreate: this._handleCreate,
        });
    }
}
KeyParametersPlugin.title = 'Key Parameters';
KeyParametersPlugin.desctiption = 'Control keycap parameters like legends, colors etc.';
KeyParametersPlugin.id = 'com.protokb.key_parameters';
